/*
SCSS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: $icon-home-width;
}

The large array-like variables contain all information about a single icon
$icon-home: x y offset_x offset_y width height total_width total_height image_path;

At the bottom of this section, we provide information about the spritesheet itself
$spritesheet: width height image $spritesheet-sprites;
*/
$icon-fb-name: 'icon-fb';
$icon-fb-x: 0px;
$icon-fb-y: 0px;
$icon-fb-offset-x: 0px;
$icon-fb-offset-y: 0px;
$icon-fb-width: 8px;
$icon-fb-height: 13px;
$icon-fb-total-width: 108px;
$icon-fb-total-height: 13px;
$icon-fb-image: '../img/sprite.png';
$icon-fb: (0px, 0px, 0px, 0px, 8px, 13px, 108px, 13px, '../img/sprite.png', 'icon-fb', );
$icon-mail-name: 'icon-mail';
$icon-mail-x: 58px;
$icon-mail-y: 0px;
$icon-mail-offset-x: -58px;
$icon-mail-offset-y: 0px;
$icon-mail-width: 15px;
$icon-mail-height: 10px;
$icon-mail-total-width: 108px;
$icon-mail-total-height: 13px;
$icon-mail-image: '../img/sprite.png';
$icon-mail: (58px, 0px, -58px, 0px, 15px, 10px, 108px, 13px, '../img/sprite.png', 'icon-mail', );
$icon-rss-name: 'icon-rss';
$icon-rss-x: 28px;
$icon-rss-y: 0px;
$icon-rss-offset-x: -28px;
$icon-rss-offset-y: 0px;
$icon-rss-width: 10px;
$icon-rss-height: 10px;
$icon-rss-total-width: 108px;
$icon-rss-total-height: 13px;
$icon-rss-image: '../img/sprite.png';
$icon-rss: (28px, 0px, -28px, 0px, 10px, 10px, 108px, 13px, '../img/sprite.png', 'icon-rss', );
$icon-twitter-name: 'icon-twitter';
$icon-twitter-x: 93px;
$icon-twitter-y: 0px;
$icon-twitter-offset-x: -93px;
$icon-twitter-offset-y: 0px;
$icon-twitter-width: 15px;
$icon-twitter-height: 10px;
$icon-twitter-total-width: 108px;
$icon-twitter-total-height: 13px;
$icon-twitter-image: '../img/sprite.png';
$icon-twitter: (93px, 0px, -93px, 0px, 15px, 10px, 108px, 13px, '../img/sprite.png', 'icon-twitter', );
$spritesheet-width: 108px;
$spritesheet-height: 13px;
$spritesheet-image: '../img/sprite.png';
$spritesheet-sprites: ($icon-fb, $icon-mail, $icon-rss, $icon-twitter, );
$spritesheet: (108px, 13px, '../img/sprite.png', $spritesheet-sprites, );

/*
The provided mixins are intended to be used with the array-like variables

.icon-home {
  @include sprite-width($icon-home);
}

.icon-email {
  @include sprite($icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

/*
The `sprites` mixin generates identical output to the CSS template
  but can be overridden inside of SCSS

@include sprites($spritesheet-sprites);
*/
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}
